._wrap {
    display: flex;
    justify-content: center;
    margin-top: 40px ;
}

._ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

._ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #fff;
    border-radius: 50%;
    animation: _ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(0 173 255) transparent transparent transparent;
}

._ring div:nth-child(1) {
    animation-delay: -0.45s;
}

._ring div:nth-child(2) {
    animation-delay: -0.3s;
}

._ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes _ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}